.timerPanel {
  width: 380px;
  right: 0;
  background-color: #070707;
  border-radius: 1.6rem;
  padding: 1rem 1.5rem;
  color: #fff;
  font-family: "Roboto", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.timerPanelTitle {
  text-align: center;
  font-family: "Montserrat";
  color: #f3b064;
  font-size: 2.4rem;
}

/* Input */
.timerConfig {
  margin: 1.5rem 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}
.timerInputItem {
  background-color: #14141d;
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  border-radius: 1rem;
  color: #f3b064;
  font-family: "Montserrat";
}
.timerInputLabel {
  font-size: 0.9rem;
}
.timerInput {
  width: 50px;
  padding: 2px 5px;
}
.timerInput::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.submitBtn {
  background-color: #f3b064;
  color: #000;
  font-weight: 600;
  padding: 8px 20px;
  font-family: "Montserrat";
  cursor: pointer;
  border-radius: 0.8rem;
  margin-bottom: 1rem;
  transition: all 0.3s;
}
.submitBtn:hover {
  background-color: #e98b1f;
}

/* Timer */

.timeLeftContainer {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.hours,
.minutes,
.seconds {
  width: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.digit {
  font-size: 4rem;
  font-family: "Montserrat";
  background-color: #14141d;
  padding: 2px 3px;
  border-radius: 10px;
}
.colon {
  font-size: 2rem;
  padding-left: 4px;
  padding-right: 4px;
}

/* Control */
.controlContainer {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  margin-top: 2rem;
  margin-bottom: 1rem;
}
.startBtn,
.pauseBtn,
.resumeBtn,
.resetBtn {
  width: 80px;
  background-color: #f3b064;
  color: #000;
  font-weight: 600;
  padding: 8px;
  font-family: "Montserrat";
  cursor: pointer;
  border-radius: 0.8rem;
  transition: all 0.3s;
  text-align: center;
}
.startBtn:hover,
.pauseBtn:hover,
.resumeBtn:hover,
.resetBtn:hover {
  background-color: #e98b1f;
}

@media screen and (max-width: 600px) {
  .timerPanel {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 60px;
    border-radius: 0;
    height: calc(100% - 70px);
    gap: 30px;
  }
}
