.todoPanel {
  width: 380px;
  right: 0;
  background-color: #070707;
  border-radius: 1.6rem;
  padding: 1rem 1.5rem;
  color: #fff;
  font-family: "Roboto", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.todoPanelTitle {
  text-align: center;
  font-family: "Montserrat";
  color: #f3b064;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}

/* Todo Input Container */

.todoInputContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 1rem;
}
.todoInput {
  padding: 4px 10px;
  width: 65%;
  border-radius: 1rem;
  font-size: 1.15rem;
}
.todoInput:focus {
  outline: 0;
  border: 2px solid #1bdb1b;
}
.todoInputAddBtn {
  width: 70px;
  background-color: #f3b064;
  color: #000;
  padding: 8px;
  border-radius: 0.8rem;
  text-align: center;
  font-weight: 600;
}
.todoInputAddBtn:hover {
  background-color: #e98b1f;
  transition: all 0.3s;
}

/* Todo Container */
.todoContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: scroll;
  height: 200px;
}
.todoContainer::-webkit-scrollbar {
  display: none;
}
.todoItem {
  width: 100%;
  margin: 0.5rem 0;
  min-height: 30px;
  background-color: #fff;
  border-radius: 1rem;
  color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
  border: 2px solid #e98b1f;
  transition: all 0.3s;
  cursor: pointer;
  font-size: 1.15rem;
}
.todoItem:hover {
  background-color: #e98b1f;
}
.todoItemChecked {
  background-color: rgb(0, 255, 64);
  width: 100%;
  margin: 0.5rem 0;
  min-height: 30px;
  border-radius: 1rem;
  color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
  border: 2px solid rgb(0, 255, 64);
  transition: all 0.3s;
  cursor: pointer;
}
.todoItemChecked:hover {
  background-color: rgb(27, 212, 74);
}
.todoItemText {
  display: block;
  width: 280px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.deleteIcon {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

@media screen and (max-width: 600px) {
  .todoPanel {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 60px;
    border-radius: 0;
    height: calc(100% - 70px);
  }
  .todoPanelTitle {
    margin-top: 1.5rem;
  }
  .todoContainer {
    height: 500px;
  }
  .todoItem {
    height: 40px;
  }
}
