.changeScreenPanel {
  width: 350px;
  right: 0;
  background-color: #070707;
  border-radius: 1.5rem;
  padding: 1.5rem;
  display: flex;
  gap: 30px;
  flex-direction: column;
  align-items: center;
}
.changeScreenPanelTitle {
  text-align: center;
  font-family: "Montserrat";
  color: #f3b064;
  font-size: 1.8rem;
}
.changeScreenItem {
  width: 320px;
  height: 190px;
  border-radius: 1rem;
  cursor: pointer;
  filter: saturate(0);
  transition: all 0.3s;
}
.changeScreenItem img {
  width: 100%;
  height: 100%;
  border-radius: 1rem;
}

.changeScreenItemActive {
  width: 320px;
  height: 190px;
  border-radius: 1rem;
  cursor: pointer;
  transition: all 0.3s;
  position: relative;
}

.changeScreenItemActive img {
  width: 100%;
  height: 100%;
  border-radius: 1rem;
}
.changeScreenItemActive::after {
  content: "";
  position: absolute;
  right: 20px;
  top: 20px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  transition: all 0.3s;
  background-color: #f09d3f;
}
@media screen and (max-width: 600px) {
  .changeScreenPanel {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 60px;
    border-radius: 0;
    height: calc(100% - 70px);
  }
  .todoPanelTitle {
    margin-top: 1.5rem;
  }
  .todoContainer {
    height: 500px;
  }
}
