.popupContainer {
  position: absolute;
  z-index: 5;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  height: 8rem;
  margin: auto;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.LateralMenu {
  border-radius: 35px;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 4rem;
  margin-right: 1.5rem;
  overflow: hidden;
}
.LateralMenu.noises {
  border-radius: 0 35px 35px 35px;
}

.LateralMenu.timer {
  border-radius: 35px 35px 35px 0px;
}

.divisor {
  width: 80%;
  height: 1px;
  background-color: #fff;
  opacity: 0.5;
}
/* MenuItemNoises */
.menuItemNoises {
  color: #fff;
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  cursor: pointer;
  opacity: 0.5;
  transition: all 0.3s;
}
.menuItemNoises:hover {
  color: #fff;
  opacity: 1;
}

.menuItemNoisesActive {
  color: #f3a952;
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  cursor: pointer;
  transition: all 0.3s;
  background-color: #000;
}
