@media screen and (max-width: 600px) {
  .popupContainer {
    width: 100%;
    top: auto;
    height: 70px;
  }
  .LateralMenu {
    flex-direction: row;
    width: 100%;
    margin: 0;
    border-radius: 0;
    justify-content: space-evenly;
    height: 100%;
  }
  .divisor {
    display: none;
  }

  .LateralMenu.noises {
    border-radius: 0;
  }

  .LateralMenu.timer {
    border-radius: 0;
  }

  .menuItemNoises,
  .menuItemTodo,
  .menuItemChangeScreen,
  .menuItemTimer,
  .menuItemNoisesActive,
  .menuItemTodoActive,
  .menuItemChangeScreenActive,
  .menuItemTimerActive {
    width: 25%;
    height: 100%;
  }
}
