.clock {
  position: absolute;
  top: 35px;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  opacity: 0.85;
  font-size: 2.2rem;
  font-family: "Orbitron";
  letter-spacing: 5px;
}
@media screen and (max-width: 600px) {
  .clock {
    font-size: 1.75rem;
    z-index: -1;
  }
}
