/* Theme Container */

.themeContainer {
  position: absolute;
  top: 35px;
  right: 35px;
}

.checkbox {
  position: absolute;
  left: 0;
  top: 0;
  height: 28px;
  width: 54px;
  z-index: 999;
  cursor: pointer;
  opacity: 0;
}
.label {
  background-color: #f3a952;
  background-color: rgba(0, 0, 0, 0.25);
  border-radius: 50px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  position: relative;
  height: 26px;
  width: 50px;
  transform: scale(1.2);
  transition: all 0.3s;
}

.label .ball {
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: 2px;
  left: 2px;
  height: 22px;
  width: 22px;
  transform: translateX(0px);
  transition: transform 0.2s linear;
}

.checkbox:checked + .label .ball {
  transform: translateX(24px);
}
.checkbox:checked + .label {
  background-color: #f3a952;
}
.checkbox:focus + .label {
  box-shadow: 0 0 0 2px rgb(0 0 0 / 10%);
}
.checkbox:hover + .label {
  opacity: 0.85;
}

/* Weather Container */
.weatherContainer {
  position: absolute;
  top: 150px;
  right: 35px;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 2rem;
  padding: 4px;
  padding: 21px;
  border: 2px solid #fff;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.5s;
  background-color: rgba(0, 0, 0, 0.8);
}
.weatherContainer:hover {
  color: #ddd;
  border-color: #ddd;
  transform: scale(1.05);
}
.weatherContainerActive {
  position: absolute;
  top: 150px;
  right: 35px;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #72c9ff;
  font-size: 2rem;
  padding: 4px;
  padding: 21px;
  border: 2px solid #72c9ff;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.5s;
  background-color: rgba(0, 0, 0, 0.8);
}
.weatherContainerActive:hover {
  transform: scale(1.05);
}

/* FullScreen Btn */
.toggleFullScreen {
  position: absolute;
  top: 90px;
  right: 45px;
  cursor: pointer;
}
.toggleFullScreen img {
  width: 25px;
  height: 25px;
}
