.background {
  width: 100%;
  height: 100%;
  position: relative;
}
.videoIn {
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
  opacity: 1;
  transition: opacity 0.5s;
  z-index: -999;
}
.videoOut {
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
  opacity: 0;
  transition: opacity 0.5s;
  z-index: -999;
}
.logo {
  width: 200px;
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (max-width: 600px) {
  .logo {
    width: 140px;
    transform: translate(-20px, 15px);
  }
  .videoIn,
  .videoOut {
    object-position: 8% 100%;
  }
}
