* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
.App {
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
  animation: opacity 1.3s linear;
}
@keyframes opacity {
  0% {
    opacity: 0.3;
  }
  100% {
    opacity: 1;
  }
}
