.musicControlContainer {
  position: absolute;
  width: 100%;
  height: 3.5rem;
  margin: 1rem 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.text {
  position: absolute;
  color: #fff;
  font-family: "Roboto";
  font-weight: 500;
  opacity: 0.7;
  font-size: 1.1rem;
  left: 35px;
}

/* Icon */
.previous,
.next {
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  transition: opacity 0.15s ease-out;
}
.play,
.pause {
  width: 4rem;
  height: 4rem;
  cursor: pointer;
  transition: all 0.4s;
}

.previous:hover,
.next:hover,
.play:hover,
.pause:hover {
  opacity: 0.65;
}

/* Embedded Clip on Youtube */

/* Toggle Clip Container */
.toggleClipContainer {
  position: absolute;
  top: 250px;
  left: 35px;
  width: 4rem;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 50%;
  font-size: 1.8rem;
  cursor: pointer;
  transition: all 0.3s;
}
.iconClip {
  color: #fff;
  opacity: 0.6;
  transition: all 0.3s;
}
.toggleClipContainer:hover .iconClip {
  opacity: 1;
}
.toggleClipContainerActive {
  position: absolute;
  top: 250px;
  left: 35px;
  width: 4rem;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 1);
  border-radius: 50%;
  font-size: 1.8rem;
  cursor: pointer;
}
.toggleClipContainerActive .iconClip {
  color: #f3a952;
  opacity: 1;
}

/* Clip Container  */
.clipContainer {
  position: absolute;
  top: 320px;
  padding: 1rem 2rem;
  width: 350px;
  left: 35px;
  background-color: #070707;
  border-radius: 1rem;
  color: #fff;
  opacity: 0;
  transform: translateX(-110%);
  transition: all 0.5s ease-out;
}

.clipContainerTitle {
  color: #f3b064;
  text-align: center;
  margin-bottom: 1rem;
  font-family: "Montserrat";
}
.inputContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

/* Input URl */
.inputUrl {
  border: none;
  width: 180px;
  padding: 4px 8px;
  border-radius: 1rem;
  font-size: 1.1rem;
}
.inputUrl:focus {
  outline: 0;
  border: 2px solid #1bdb1b;
}
.inputUrlSubmitBtn {
  padding: 5px 10px;
  background-color: #f3b064;
  color: #000;
  font-family: "orbitron";
  font-size: 1rem;
  font-weight: 600;
  border-radius: 0.8rem;
}
.inputUrlSubmitBtn:hover {
  background-color: #e98b1f;
}

@media screen and (max-width: 600px) {
  .musicControlContainer {
    bottom: 80px;
  }
  .text {
    display: none;
  }
  .toggleClipContainer,
  .toggleClipContainerActive {
    left: 10px;
  }
  .clipContainer {
    left: 10px;
    width: 345px;
  }

  .previous,
  .next {
    width: 6rem;
    height: 4rem;
  }
  .play,
  .pause {
    width: 8rem;
    height: 5rem;
  }
}
