/* Panel */
.noisesPanel {
  width: 340px;
  height: 475px;
  right: 0;
  background-color: #070707;
  border-radius: 1.6rem;
  padding: 1rem 2rem;
  color: #fff;
  font-family: "Roboto", sans-serif;
  overflow: hidden;
}

/* Mood */
.moodTitle {
  margin-bottom: 1rem;
  text-align: center;
  color: #f3b064;
}
.moodContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.moodItem {
  width: 5rem;
  height: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-color: #14141d;
  border-radius: 1rem;
  cursor: pointer;
}
.moodItem.active .moodItemIcon {
  color: #f3b064;
  transition: all 0.5s;
}
.moodItemIcon {
  font-size: 2rem;
  color: #2c2c2e;
}
.moodItemTitle {
  font-size: 1rem;
}
/* Volume */
.MuiSlider-root {
  color: #f3b064;
}
.volumeContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 0;
  opacity: 1;
  transition: all 0.5s;
}
.volumeItemIcon {
  width: 10%;
  height: 20px;
  color: #f3b064;
}
/* Noises */
.noisesContainer {
  overflow: scroll;
  height: 150px;
}
.noisesContainer::-webkit-scrollbar {
  display: none;
}
.noisesTitle {
  margin-bottom: 1rem;
  color: #f3b064;
}
.noisesItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.noisesItemTitle {
  font-size: 14px;
  opacity: 0.5;
}

/* AngleContainer */
.angleContainer {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin-top: 8px;
}
.angle {
  opacity: 0.6;
  width: 25px;
  height: 25px;
}

@media screen and (max-width: 600px) {
  .noisesPanel {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 60px;
    border-radius: 0;
    height: calc(100% - 70px);
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
  .moodTitle {
    font-size: 2.5rem;
  }
  .moodContainer {
    margin: 2rem 0;
    justify-content: space-evenly;
  }
  .volumeContainer {
    padding: 0;
  }
  .noisesTitle {
    font-size: 2rem;
  }
  .noisesContainer {
    height: 250px;
  }
  .noisesItemTitle {
    font-size: 1.2rem;
  }
}
